export default {
  layout: "admin",
  data() {
    return {
      showForm: false,
      loading: false,
      model: "popup",
      /* rawCriterion */
      rawCriterion: {
        search: {
          key: "name",
          term: "",
        },
        filters: {
          type: "default",
        },
        sortBy: {},
        limit: 20,
      },
      list: [],
      sortByFields: [
        "name",
        "category",
        "visibility",
        "delay",
        "status",
      ],
      headings: [
        "_id",
        "name",
        "category",
        "visibility",
        "delay",
        "status",
      ],
    };
  },
  async mounted() {
    // await this.fetchList();
  },
  methods: {
    updateList() {
      this.$refs.pagination.fetchResults();
    },
    sortToggled(sortBy) {
      console.log(sortBy);
      this.rawCriterion = {
        ...this.rawCriterion,
        sortBy,
      };
    },
    documentFetched(doc) {
      this.showForm = true;
      this.editMode = true;
      this.$refs.updateComponent.populateForm(doc);
      this.assignImages("imageUploader", doc.image);
    },
    resultsFetched(result) {
      if (result.docs.length === 0) {
        this.list = [];
        return;
      }

      /* extract list */
      this.list = result.docs.map(
        ({ _id, name, category, visibility, delay, status }) => {
          return {
            _id,
            name,
            category,
            visibility,
            delay,
            status,
          };
        }
      );
    },
    assignImages(ref, image) {
      if (image === "" || image === undefined) return;
      setTimeout(() => {
        this.$refs.updateComponent.$refs[ref].assignImages([
          {
            _id: "",
            mainImage: false,
            path: image,
          },
        ]);
      }, 1200);
    },
  },
};
